@import '../config/_colors';
@import '../config/_variables';
@import '../config/_breakpoints';
@import '../config/_animations';

.nav {
  height: $nav-height;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 110px;
  background: $color-white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  animation: 0.3s fadein forwards ease-out;
  z-index: 2;
  @include media($breakpoint-md) {
    padding: 0 40px; }
  @include media($breakpoint-sm) {
    padding: 0 24px;
    height: 75px; } }

.nav__header {
  font-size: 24px;
  font-weight: 500;
  display: flex;
  align-items: center;
  color: $color-black;
  flex-grow: 1;
  @include media($breakpoint-sm) {
    font-size: 20px; }
  @include media($breakpoint-xs) {
    font-size: 16px; } }

.nav__logo {
  width: 32px;
  margin-right: 8px; }

.nav__link {
  font-size: 18px;
  font-weight: 300;
  color: $color-black;
  margin-right: 32px;
  text-decoration: none;
  transition: 0.2s color;
  cursor: pointer;

  @include media($breakpoint-md) {
    display: none; } }

.nav__link:hover {
  color: $color-gold; }

.nav__button {
  background: linear-gradient(to bottom, $color-gold, $color-gold-dark);
  color: $color-white;
  border: 1px solid $color-gold;
  border-radius: 3px;
  padding: 8px 16px;
  font-size: 18px;
  font-family: Roboto, sans-serif;
  font-weight: 300;
  text-decoration: none;
  @include media($breakpoint-sm) {
    font-size: 16px; }
  @include media($breakpoint-xs) {
    font-size: 12px; } }
