@import '../config/_colors';
@import '../config/_variables';
@import '../config/_breakpoints';

.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 0 $hero-padding;
  opacity: 0;
  animation: 0.3s fadeindown 0.2s forwards ease-out;

  @include media($breakpoint-lg) {
    flex-direction: column-reverse;
    padding: 0 32px; } }

.hero__left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 40%;
  margin-top: -64px;

  @include media($breakpoint-lg) {
    align-items: center;
    width: 100%;
    margin-top: 32px; } }

.hero__header {
  font-size: 48px;
  font-weight: 500;
  margin-bottom: 0;
  max-width: 90vw;
  color: $color-black;

  @include media($breakpoint-lg) {
    text-align: center; }
  @include media($breakpoint-md) {
    font-size: 36px; }
  @include media($breakpoint-sm) {
    font-size: 30px;
    margin-bottom: 0; }
  @include media($breakpoint-xs) {
    font-size: 24px; } }

.hero__subheader {
  font-size: 24px;
  font-weight: 300;
  max-width: 90vw;
  margin-top: 16px;
  color: $color-black;

  @include media($breakpoint-lg) {
    text-align: center; }
  @include media($breakpoint-md) {
    font-size: 20px; }
  @include media($breakpoint-sm) {
    font-size: 18px; }
  @include media($breakpoint-xs) {
    font-size: 16px; } }

.hero__button {
  height: 60px;
  font-size: 32px;
  font-weight: 300;
  width: 360px;
  background: linear-gradient(to bottom, $color-gold, $color-gold-dark);
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: white;
  max-width: 90vw;

  @include media($breakpoint-sm) {
    font-size: 24px; } }

.hero__image {
  margin-top: 32px;
  margin-bottom: -32px;
  width: 392px;
  height: 600px;
  max-width: 90vw;
  max-height: 60vh;
  margin-right: -16px;

  @include media($breakpoint-md) {
    width: 360px;
    height: 460px; }
  @include media($breakpoint-sm) {
    width: 240px;
    height: 300px;
    margin-top: 24px; }
  @include media($breakpoint-xs) {
    width: 160px;
    height: 200px; } }
