@import '../config/_colors';
@import '../config/_breakpoints';

.step {
  height: 500px;
  width: 100%;

  @include media($breakpoint-lg) {
    height: auto; } }

.step:nth-of-type(even) {
  background: $color-grey-light;

  .step__content {
    flex-direction: row-reverse;

    @include media($breakpoint-lg) {
      flex-direction: column; } } }

.step__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  margin: 0 auto;
  width: 750px;

  @include media($breakpoint-lg) {
    flex-direction: column;
    padding: 48px;
    width: auto; } }

.step__side {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  height: 300px;
  width: 47%;
  margin-top: 32px;
  opacity: 0;

  @include media($breakpoint-lg) {
    width: 100%;
    align-items: center;
    height: auto;
    margin-top: 0; } }

.step:nth-of-type(even) .step__side.active {
  animation: 0.3s fadeleft 0.2s forwards ease-out;

  @include media($breakpoint-lg) {
    animation: 0.3s fadein 0.2s forwards ease-out; } }

.step:nth-of-type(odd) .step__side.active {
  animation: 0.3s faderight forwards ease-out;

  @include media($breakpoint-lg) {
    animation: 0.3s fadein 0.2s forwards ease-out; } }

.step__header {
  font-size: 32px;
  font-weight: 400;
  color: $color-black;
  margin-bottom: 16px;
  max-width: 90vw;

  @include media($breakpoint-lg) {
    text-align: center;
    margin-top: 0; }
  @include media($breakpoint-sm) {
    font-size: 24px; } }

.step__info {
  font-size: 18px;
  font-weight: 300;
  color: $color-black;
  margin: 0;
  max-width: 90vw;

  @include media($breakpoint-lg) {
    text-align: center; }
  @include media($breakpoint-sm) {
    font-size: 16px; } }

.step__image {
  width: 47%;
  opacity: 0;

  @include media($breakpoint-lg) {
    margin-top: 32px; }
  @include media($breakpoint-sm) {
    width: 100%; } }

.step:nth-of-type(even) .step__image.active {
  animation: 0.3s faderight forwards ease-out;

  @include media($breakpoint-lg) {
    animation: 0.3s fadein 0.2s forwards ease-out; } }

.step:nth-of-type(odd) .step__image.active {
  animation: 0.3s fadeleft 0.2s forwards ease-out;

  @include media($breakpoint-lg) {
    animation: 0.3s fadein 0.2s forwards ease-out; } }

.step__image--lock {
  width: 40%; }
